<template>
	<view class="result-wrapper">
		<view
			class="result-list"
			v-for="(item, index) of searchResult.resultList"
			:key="index"
			@click="toSong(item.id, item.name)"
		>
			<view class="song">
				<view class="song-name" v-html="keywordHignlight(item.name)"></view>
				<view class="song-details">
					<view class="" v-html="iconTag(item.privilege.fee,'vip')"></view>
					<view class="" v-html="iconTag(item.privilege.flag,'cr')"></view>
					<view class="" v-html="iconTag(item.privilege.maxbr, 'sq')"></view>
					<view class="artist" v-html="keywordHignlight(getAr(item.ar)+' - '+item.al.name)"></view>
				</view>
			</view>
			<view class="play-btn">
				
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: "SearchResult",
		props: {
			searchResult: {
				keyword: String,
				resultList: Array
			}
		},
		computed: {
			keywordHignlight(keyword) {
				return (item) => {
					let key = this.searchResult.keyword
					return item.replace(key, "<span class='Highlight'>" + key + "</span>")
				}
			},
		},
		methods: {
			getAr(ar) {
				if (ar.length === 1) {
					return ar[0].name
				} else {
					let user = ""
					ar.forEach((item, index) => {
						if (index !== ar.length - 1) {
							user += item.name + " / "
						} else {
							user += item.name
						}
					})
					return user;
				}
			},
			iconTag(flag, tag) {
				
				switch(tag) {
					case "vip": 
						if(flag == 1) {
							return "<div class='vip'></div>";
						}		
					case "cr":  
						if(flag >= 60 && flag <= 70) {
							return "<div class='cr'></div>";
						}
					case "sq": 
						if( flag == 999000) {
							return "<div class='sq'></div>";
						}
				}
	
			},
			toSong(id, name) {
				uni.navigateTo({
					url:"../../pages/Song/Song?id="+ id + "&name="+ name,
					animationType: "pop-in"
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.result-wrapper {
		.result-list {
			position: relative;
			.song {
				box-sizing: border-box;
				border: 0 solid rgba(0,0,0,.1);
				border-bottom-width: 2rpx;
				padding: 20rpx 0;
				display: inline-block;
				width: 100%;
				.song-name {
					width: 85%;
					font-size: 31.2525rpx;
					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
					word-break: normal;
					line-height: 1.5;
				}
				.song-details {
					display: flex;
					align-items: center;
					.artist {
						color: #999;
						font-size: 22.9185rpx;
					}
				}
				
			}
			.play-btn {
				position: absolute;
				top: 37.503rpx;
				right: 0;
				width: 50.004rpx;
				height: 50.004rpx;
				background-size: 100% 100%;
				background-image: url(data:image/svg+xml;base64,);
			}
		}
		
	}
</style>
